td {
  font-weight: bold;
  color: #1713ff;
  text-align: center;
}

@media screen and (max-width : 850px) {
    .table-head {
        flex-direction: column;
      }
}
.tic {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 3rem;
  font-weight: bolder;
  color: rgb(75, 0, 195);
}

.tiw {
  display: flex;
  align-items: center;
  justify-content: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.ti1 {
  justify-self: center;
  height: 450px;
  width: 100%;
}
.ti2 {
  justify-self: center;
  height: 450px;
  width: 100%;
}

@media screen and (max-width: 1024px) {
  .tiw {
    flex-direction: column;
    padding-left: 0px;
  }
  .ti1 {
    justify-self: center;
    height: auto;
    width: 100%;
  }
  .ti2 {
    justify-self: center;
    height: auto;
    width: 100%;
  }
}


.hero {
  height: 100vh;
  background: url(/static/media/hero-image.a157b621.jpg);
  background-position: center;
  padding:  1.5rem 4rem 2.5rem 4rem;
}
.home-qcs {
  min-height: 110vh;
  padding-top: 5rem;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
}
.subheading_home{
    /* color : red; */
    font-size: 1.5rem;
}
.heading_home{
    color: rgb(51, 45, 105);
    font-size: 3.6rem;
}
.slanted_home {
  position: absolute;
  width: 100%;
  padding: 0.5rem 1rem;
  height: 5.5rem;
  width: 50rem;
  background: white;
  transform: skew(-12deg); 
}
.h_home{
  position: absolute;
  color: #0000fe;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 0.5rem 1rem;
  text-shadow: 2px 2px 2px #c9b8b8;
}
.sh_home{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1.6rem;
  line-height: 2.5rem;
  font-weight:bolder;
  text-align: left;
  text-shadow: 4px 4px 4px #000000;
}
@media screen and (max-width: 600px) {
  .hero {
    padding: 1rem 1rem;
  }
}
